@media all and (max-width: 865px) {
  header {
    position: fixed;
    padding: 0;
    z-index: 1;
    background-color: #fcdb00;
    height: 10vh;
    opacity: 0.8;
    min-height: 60px;

    .container {
      margin: 0;
      z-index: -1;

      .logo {
        margin-left: 15px;
      }

      .top-menu {
        display: none;
      }

      .dropdown {
        display: flex;
        position: absolute;
        width: 100%;
        flex-direction: column;
        z-index: -1;

        .drop-down-button {
          background-color: #fcdb00;
          display: flex;
          align-items: center;
          justify-content: flex-end;
          z-index: 1;

          .hamburger {
            display: flex;
            align-items: center;
            height: 10vh;

            .menu {
              font-size: 1.5rem;
              font-weight: bolder;
              margin-left: 20px;
            }
          }

          .hamburger:focus {
            outline: none;
          }

        }

        .dropdown-content {
          position: absolute;
          transition: top 0.5s;
          top: -500px;
          width: 100%;
          display: flex;
          flex-direction: column;
          z-index: -5;
          box-shadow: 0px 0px 39px -5px rgba(0, 0, 0, 0.75);

          a {
            width: 100%;
            height: 15vh;
            text-align: center;
            text-decoration: none;
            color: black;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #fcdb00;

            &:hover {
              background-color: #000000;
              color: white;
            }

            span {
              font-size: 1.5rem;

              &:hover {
                color: white;
              }
            }
          }
        }
      }
    }
  }

  main {
    .hero {
      .container {
        text-align: center;

        .slider {
          .slider-item {
            justify-content: center;
          }

          .iPad-mini-white-facing {
            display: none;
          }
        }
      }
    }

    .features {
      height: 160vh;
      min-height: 1080px;

      .container {
        .section-title {
          margin: 0;
        }

        .features-grid {
          .row {
            display: flex;
            flex-direction: column;

            &:not(:last-child) {
              margin: 0;
            }

            .featured {
              margin-bottom: 20px;
              justify-content: center;
            }
          }
        }
      }
    }
  }

  footer {
    .container {
      .social-content {
        z-index: 1;
      }
      .footer-widgets {
        flex-direction: column;
        .address {
          margin-bottom: 50px
        }
        .links-group {
          .column {
            margin-right: 20px;
            margin-bottom: 20px;
            &:first-child {
              margin-left: 0;
            }
          }
        }
      }


      .email {
        flex-direction: column;


      }

      .subcrible {
        form {
          input[type="text"] {
            width: 30%;
          }
        }
      }
    }
  }
}