footer .container .footer-widgets {
  display: flex;
  color: white;
  padding: 0 80px 0 13px;
}
footer .container .footer-widgets .address {
  max-width: 185px;
  margin-right: 123px;
}
footer .container .footer-widgets .address p {
  margin: 0;
}
footer .container .footer-widgets .links-group {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
footer .container .footer-widgets .links-group .column {
  display: flex;
  flex-direction: column;
}
footer .container .footer-widgets .links-group .column:first-child {
  margin-left: 30px;
}
footer .container .footer-widgets .links-group .column a {
  color: white;
  text-decoration: none;
}
footer .container .footer-widgets .links-group .column a:hover {
  color: #fcdb00;
  cursor: pointer;
}
footer .container .email-message {
  position: absolute;
  background-color: #fcdb00;
  width: 50%;
  height: 20%;
  border-radius: 10px;
  margin: 0 auto;
  left: 0;
  font-size: 1rem;
  right: 0;
  -webkit-box-shadow: 0px 0px 24px 0px #ebd403;
  -moz-box-shadow: 0px 0px 24px 0px #ebd403;
  box-shadow: 0px 0px 24px 0px #ebd403;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: 0.3s;
}
footer .container .email-message .checkmark {
  display: inline-block;
}
footer .container .email-message .checkmark:after {
  content: '';
  display: block;
  width: 50px;
  height: 100px;
  border: solid #000;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}
footer .container .email-active {
  opacity: 1;
}
.container {
  max-width: 960px;
  margin: 0 10px;
}
@media all and (max-width: 865px) {
  header {
    position: fixed;
    padding: 0;
    z-index: 1;
    background-color: #fcdb00;
    height: 10vh;
    opacity: 0.8;
    min-height: 60px;
  }
  header .container {
    margin: 0;
    z-index: -1;
  }
  header .container .logo {
    margin-left: 15px;
  }
  header .container .top-menu {
    display: none;
  }
  header .container .dropdown {
    display: flex;
    position: absolute;
    width: 100%;
    flex-direction: column;
    z-index: -1;
  }
  header .container .dropdown .drop-down-button {
    background-color: #fcdb00;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    z-index: 1;
  }
  header .container .dropdown .drop-down-button .hamburger {
    display: flex;
    align-items: center;
    height: 10vh;
  }
  header .container .dropdown .drop-down-button .hamburger .menu {
    font-size: 1.5rem;
    font-weight: bolder;
    margin-left: 20px;
  }
  header .container .dropdown .drop-down-button .hamburger:focus {
    outline: none;
  }
  header .container .dropdown .dropdown-content {
    position: absolute;
    transition: top 0.5s;
    top: -500px;
    width: 100%;
    display: flex;
    flex-direction: column;
    z-index: -5;
    box-shadow: 0px 0px 39px -5px rgba(0, 0, 0, 0.75);
  }
  header .container .dropdown .dropdown-content a {
    width: 100%;
    height: 15vh;
    text-align: center;
    text-decoration: none;
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fcdb00;
  }
  header .container .dropdown .dropdown-content a:hover {
    background-color: #000000;
    color: white;
  }
  header .container .dropdown .dropdown-content a span {
    font-size: 1.5rem;
  }
  header .container .dropdown .dropdown-content a span:hover {
    color: white;
  }
  main .hero .container {
    text-align: center;
  }
  main .hero .container .slider .slider-item {
    justify-content: center;
  }
  main .hero .container .slider .iPad-mini-white-facing {
    display: none;
  }
  main .features {
    height: 160vh;
    min-height: 1080px;
  }
  main .features .container .section-title {
    margin: 0;
  }
  main .features .container .features-grid .row {
    display: flex;
    flex-direction: column;
  }
  main .features .container .features-grid .row:not(:last-child) {
    margin: 0;
  }
  main .features .container .features-grid .row .featured {
    margin-bottom: 20px;
    justify-content: center;
  }
  footer .container .social-content {
    z-index: 1;
  }
  footer .container .footer-widgets {
    flex-direction: column;
  }
  footer .container .footer-widgets .address {
    margin-bottom: 50px;
  }
  footer .container .footer-widgets .links-group .column {
    margin-right: 20px;
    margin-bottom: 20px;
  }
  footer .container .footer-widgets .links-group .column:first-child {
    margin-left: 0;
  }
  footer .container .email {
    flex-direction: column;
  }
  footer .container .subcrible form input[type="text"] {
    width: 30%;
  }
}
